// Importar mdulos necesarios
import { initializeDarkMode } from './utils/darkMode.js';
import { setupEventListeners, showError, showSuccess } from './utils/eventListeners.js';
import { initializeCharts } from './charts.js';
import { runSimulation } from './simulation.js';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import Chart from 'chart.js/auto';

// Hacer que Chart.js est disponible globalmente
window.Chart = Chart;
window.jsPDF = jsPDF;
// Hacer que runSimulation est disponible globalmente
window.runSimulation = runSimulation;

console.log('main.js cargado correctamente');

// Verificar dependencias crticas
const checkDependencies = () => {
    const missingDeps = [];
    
    if (typeof Chart === 'undefined') {
        missingDeps.push('Chart.js');
    }
    
    if (typeof jsPDF === 'undefined') {
        missingDeps.push('jsPDF');
    }
    
    return missingDeps;
};

// Funcin para inicializar la aplicacin
async function initApp() {
    console.log('Inicializando aplicacin...');
    showSuccess('Iniciando aplicacin...', 2000);
    
    try {
        // Verificar dependencias
        const missingDeps = checkDependencies();
        if (missingDeps.length > 0) {
            const errorMsg = `Faltan dependencias: ${missingDeps.join(', ')}. Recarga la pgina.`;
            console.error(errorMsg);
            showError(errorMsg);
            return;
        }
        
        // Inicializar modo oscuro
        if (typeof initializeDarkMode === 'function') {
            initializeDarkMode();
        } else {
            console.warn('initializeDarkMode no es una funcin');
        }
        
        // Configurar manejadores de eventos
        if (typeof setupEventListeners === 'function') {
            setupEventListeners();
        } else {
            console.error('setupEventListeners no es una funcin');
            showError('Error al configurar los manejadores de eventos');
        }
        
        // Inicializar grficos
        if (typeof initializeCharts === 'function') {
            try {
                window.distributionChart = initializeCharts();
                console.log('Grficos inicializados correctamente');
            } catch (chartError) {
                const errorMsg = `Error al inicializar grficos: ${chartError.message}`;
                console.error(errorMsg, chartError);
                showError(errorMsg);
            }
        } else {
            const errorMsg = 'La funcin initializeCharts no est disponible';
            console.error(errorMsg);
            showError(errorMsg);
        }
        
        console.log('Aplicacin inicializada correctamente');
        showSuccess('Aplicacin lista', 2000);
    } catch (error) {
        const errorMsg = `Error al inicializar la aplicacin: ${error.message}`;
        console.error(errorMsg, error);
        showError(errorMsg);
    }
}

// Funcin para verificar si el DOM est listo
const domReady = (callback) => {
    if (document.readyState !== 'loading') {
        callback();
    } else {
        document.addEventListener('DOMContentLoaded', callback);
    }
};

// Inicializar la aplicacin cuando el DOM est listo
document.addEventListener('DOMContentLoaded', () => {
    console.log('DOM completamente cargado, iniciando aplicacin...');
    initApp();
});

// Verificar dependencias en consola
console.log('Dependencias cargadas:', {
    'Chart.js': typeof Chart !== 'undefined',
    'jsPDF': typeof jsPDF !== 'undefined',
    'runSimulation': typeof runSimulation === 'function'
});
